:root {
    --toast-body-color: #f1d562; /*#f1c40f*/
    --toast-text-color: black;
    --toastify-color-progress-warning: tomato; /* var(--bs-primary); */
    --toastify-icon-color-warning: tomato; /* var(--bs-primary); */
}

.toast-about-container {
    color: black !important;
    color: var(--toast-text-color) !important;
    background: #BBBBBB !important;
}

.toast-success-container {
    color: black !important;
    color: var(--toast-text-color) !important;
    background: #f1d562 !important;
    background: var(--toast-body-color) !important;
    _border: 1px solid #34A853 !important;
    _border-radius: 8px !important;
    _box-shadow: 0px 1px 5px rgba(248, 175, 175, 0.1) !important;
}
._toast-success-container-after {
    overflow: hidden;
    position: relative;
}
._toast-success-container-after::after{
    top: 0;
    left: 0;
    content: '';
    width: 7px;
    height: 100%;
    position: absolute;
    display: inline-block;
    background-color: #34A853;
}

.toast-error-container {
    color: black !important;
    color: var(--toast-text-color) !important;
    background: #f1d562 !important;
    background: var(--toast-body-color) !important;
}

.toast-info-container {
    color: black !important;
    color: var(--toast-text-color) !important;
    background: #f1d562 !important;
    background: var(--toast-body-color) !important;
}
