.table-info {
	position: absolute;
    width: -webkit-fill-available;
    height: 95%;
    margin: 15px 10px 0px 0px;
    overflow-y: auto;
	_font-size: calc(var(--width-card-lg) / var(--font-size-card-coef) / 3);
    _line-height: calc(var(--width-card-lg) / var(--font-size-card-coef) / 3);/*0.8em*/
}
.table-info-text {
	font-weight: normal;
}
.table-info-warning {
	color: tomato;
}
.table-info-alarm {
	color: tomato;
	font-weight: bold;
}
