.navbar-brand {
    white-space: inherit;
    text-align: center;
}
.navbar-toggler-focus:focus {
    outline: solid !important;
	outline-color: tomato !important;
	box-shadow: none;
}
.btn-outline-light:hover {
    color: white;
    background-color: var(--bs-primary);
    border-color:     transparent; 
}
.navbar-button {
	padding: 0;
	width: 3em;
    height: auto;
}
.navbar-button-image {
	height: 2.5em;
    width: auto;
}