.flip { /* место под плитку */
	-webkit-perspective: 800;   
    perspective: 800;
	margin: 10px;
	padding: 0px;
}
.card:hover { /* подсветка активной плитки */
	border: 1px solid rgba(0, 0, 255, 0.5);
	box-shadow: 0 4px 8px 0 rgba(0, 0, 255, 0.1), 0 6px 20px 0 rgba(0, 0, 128, 0.1);
}
.flip .card { /* плитка с 3d-поворотом за 0.5 сек. */
    -webkit-transform-style: preserve-3d;
    -webkit-transition: 0.5s;
    transform-style: preserve-3d;
    transition: 0.5s;
	display: flex;
	cursor: pointer;
}
.flip .card .back { /* поворот задней поверхности плитки вокруг оси y */
    -webkit-transform: rotatey(-180deg);
	transform: rotatey(-180deg);
	position: absolute;
}
.flip .card.flipped { /* поворот плитки вокруг оси y */
	-webkit-transform: rotatey(-180deg);
    transform: rotatey(-180deg);
}
.flip .card.closed { /* поворот плитки вокруг оси x */
	-webkit-transform: rotatex(-90deg);
    transform: rotatex(-90deg);
}
._card-sm {
    width: 150px;
    height: 75px;
	font-size: 15px;
	font-weight: 300;	
}
.card-sm {
    width: var(--width-card-sm);
    height: calc(var(--width-card-sm) / var(--height-card-coef));
	font-size: calc(var(--width-card-sm) / var(--font-size-card-coef));
	font-weight: 400;
}
.card-md {
    width: var(--width-card-md);
    height: calc(var(--width-card-md) / var(--height-card-coef));
	font-size: calc(var(--width-card-md) / var(--font-size-card-coef));
	font-weight: 600;
}
.card-lg {
	width: var(--width-card-lg);
    height: calc(var(--width-card-lg) / var(--height-card-coef));
	font-size: calc(var(--width-card-lg) / var(--font-size-card-coef));
	font-weight: 800;
}
.flip .card .face { /* поверхность плитки - изначально невидима */
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	z-index: 2;
	width: 100%;
	height: 100%;
}
.flip .card .front { /* лицевая сторона плитки */
	position: absolute;
	_width: 100px;
	z-index: 1;
}
