*, :after, :before {
	box-sizing: border-box;
}
*:focus {
    outline: none;
	outline-color: tomato;
	outline-style: solid;
	outline-width: medium;
}
.form-control:focus {
	border-color: tomato;
	border-width: 1px;
	box-shadow: 0 0 0 0;
	outline-color: tomato;
	outline-style: solid;
	outline-width: medium;
}
.form-check-input:focus {
	_outline-color: tomato;
	box-shadow: 0 0 0 3px tomato;
}
#root {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	flex-flow: column nowrap;
	align-content: space-between;
	min-height: 0;
	--left-width: 200px;
	--width-card-sm: 300px;
	--width-card-md: 500px;
	--width-card-lg: 700px;
	--height-card-coef: 2;
	--font-size-card-coef: 15;
}
#top {
	background-color: blue;
	order: 1;
	height: auto;
	flex: none;
	display: none;
}
#btm {
	_background-color: gray;
	order: 3;
	height: auto;
	flex: none;
}
#content {
	order: 2;
	flex-grow: 1;
	flex-basis: auto;
	width: 100%;
	display: flex;
	flex-flow: row nowrap;
	min-height: 0;
}
#left {
