#object-list {
	margin-left: 5px;
	margin-right: 5px;
}
.object-list-item {
	margin-top: 3px;
	display: flex;
	flex-direction: column;
}
.object-list-item-active {
	margin-top: 0.5em;
	display: flex;
	flex-direction: column;
}
.object-list-item-button {
	width: auto;
	border-radius: 5px !important;
}
.object-list-item-active-dropdown {
    float: right;
    margin-right: -15px;
    margin-top: -5px;
    margin-bottom: -5px;
}
.dropdown-menu {
	_box-shadow: 10px 10px 10px 10px 15px rgba(0, 0, 0, 0.5);
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}